img{
    width: 100%;
    margin-bottom: 20px;
}
h2{
    margin-bottom: 20px;
}
p{
    color:#333;
}
.news-card{
    padding: 20px;
    border:2px solid black;
    position: relative;
}
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    width: 100%;
    position: relative;
    font-family: 'Roboto',sans-serif;
}
.navbar{
    width: 100%;
    height: 80px;
    position: fixed;
    top: 0;
    left: 0;
    padding: 0 4%;
    background: #0c111b;
    z-index: 9;
    display: flex;
    align-items: center;
}

.right-container{
    display: block;
    margin-left: auto;
}
.search-box{
    border: none;
    border-bottom: 1px solid #aaa;
    background: transparent;
    outline: none;
    height: 30px;
    color: #fff;
    width: 250px;
    text-transform: capitalize;
    font-size: 16px;
    font-weight: 500;
    transition: 0.5s;
}
.search-box:focus{
    width: 400px;
    border-color: #1f80ce;
}
.sub-btn{
    background: #1f80ce;
    height: 30px;
    padding: 0 20px;
    color: #fff;
    border-radius: 5px;
    border: none;
    outline: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 12px;
    margin: 0 10px;

}
.sub-btn:hover{
    background-color: rgb(34, 103, 153);
}
.website{
  color: white;
  font-weight: 700;
  font-size: 20px;

}
.box{
  height:3px;
  width: 70px;
  background-color: blue;
  margin-bottom: 20px;
}
.Latest{
  text-align: center;
}
@media (max-width: 60em) {
  body {
    padding: 3rem;
  }
}
.grid-class{
  display:grid; 
  grid-template-columns:repeat(3,30%);
  justify-content:space-around;
  row-gap:20px;
}
/* .grid {
  display: grid;
  width: 114rem;
  grid-gap: 6rem;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  align-items: start;
} */
@media (max-width: 60em) {
  .grid {
    grid-gap: 3rem;
  }
}
.grid__item {
  background-color:  #0c111b;
  border-radius: 0.4rem;
  overflow: hidden;
  box-shadow: 0 3rem 6rem rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: 0.2s;
}
.grid__item:hover {
  transform: translateY(-0.5%);
  box-shadow: 0 4rem 8rem rgba(0, 0, 0, 0.2);
}

.card__img {
  display: block;
  width: 100%;
  height: 18rem;
  object-fit: cover;
}
.card__content {
  text-align: center;
}
.card__header {
  font-weight: 500;
  color: #0d0d0d;
  margin-bottom: 1.5rem;
  color:white;
}
.card__text {
  letter-spacing: 0.1rem;
  color:white;
  margin-bottom: 2.5rem;

}



.button-14 {
  background-image: linear-gradient(#f7f8fa ,#e7e9ec);
  border-color: #adb1b8 #a2a6ac #8d9096;
  border-style: solid;
  border-width: 1px;
  border-radius: 3px;
  box-shadow: rgba(255,255,255,.6) 0 1px 0 inset;
  box-sizing: border-box;
  color: #0f1111;
  cursor: pointer;
  display: inline-block;
  font-family: "Amazon Ember",Arial,sans-serif;
  font-size: 14px;
  height: 29px;
  font-size: 13px;
  outline: 0;
  overflow: hidden;
  padding: 0 11px;
  text-align: center;
  text-decoration: none;
  text-overflow: ellipsis;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  margin-bottom: 27px;
}

.button-14:active {
  border-bottom-color: #a2a6ac;
}

.button-14:active:hover {
  border-bottom-color: #a2a6ac;
}

.button-14:hover {
  border-color: #a2a6ac #979aa1 #82858a;
}

.button-14:focus {
  border-color: #e77600;
  box-shadow: rgba(228, 121, 17, .5) 0 0 3px 2px;
  outline: 0;
}
@media only screen and (max-width: 550px)
{
.grid-class{
  display:grid;
  grid-template-columns: repeat(1, 100%);
}
}
@media only screen and (max-width: 430px)
{
  .search-box{
    width: 110px;
  }
  .search-box:focus{
    width: 100px;
    border-color: #1f80ce;
  }
}